$dark:  #151412;
$coffee_milk: #c7a17a;
$gray: #acacac;
$little_dark_gray: #9b9b9b;
$white: #fff;

.background- {
   &dark { background-color: $dark; color: $white; }
}
.color-{
   &dark { color: $dark; }
   &coffee-milk { color: $coffee_milk; }
   &gray { color: $gray; }
   &little-dark-gray { color: $little_dark_gray; }
   &white { color: $white; }
}
