@import "responsive.scss";
@import "color.scss";
@import "home.scss";

* {
   font-family: Marcellus, sans-serif;
   font-size: 15px;
   box-sizing: border-box;
   padding: 0;
   margin: 0;
}

.container { width: 100%; }

.row {
   display: -webkit-box;
   display: flex;
   flex-wrap: wrap;
}

h2 { font-size: 35px; line-height: 2.5; }
h3 { font-size: 22px; line-height: 2; }
h4 { font-size: 19px; line-height: 1.5; }
h5 { font-size: 14px; line-height: 1; }


.text{
   &-center { text-align: center; }
   &-right { text-align: right; }
}

.clearfix {
   overflow: auto;
   &::after {
      content: "";
      clear: both;
      display: table;
   }
}

.menu {
   position: absolute;
   width: 100%;
   padding: 0 40px;
   z-index: 1;
   .logo-img {
      width: 9em;
      float: left;
   }

   .menu-right {
      color: white;
      text-align: right;
      ul {
         display: inline-block;
         line-height: 5rem;
      }
      li {
         float: left;
         list-style-type: none;
         padding: 0 20px;
         color: #fff;
         a {
            color: #fff;
            font-weight: 400;
            text-decoration: none;
            text-transform: uppercase;
         }

         .menu-link {
            position: relative;
         }

         .menu-link:after {
            content: "";
            position: absolute;
            width: 0;
            height: 3px;
            display: block;
            margin-top: -25px;
            right: 0;
            background: #fff;
            transition: width 0.8s ease;
            -webkit-transition: width 0.8s ease;
         }

         .menu-link:hover:after {
            width: 100%;
            left: 0;
            background: #fff;
         }
      }
   }
}

