/* Mixin */
/* Set các giá trị với màn hình tương ứng */
$small-width:  576px;
$medium-width:  768px;
$large-width:  960px;
$extra-large-width:  1140px;


@mixin mobile { @media (max-width: #{$small-width - 1px}) { @content; } }  

@mixin tablet { @media (min-width: #{$small-width }) and (max-width: #{$medium-width}) { @content; } }

@mixin small-desktop { @media (min-width: #{$medium-width}) and (max-width: #{$large-width}) { @content; } }

@mixin desktop { @media (min-width: #{$large-width}) and (max-width: #{$extra-large-width}) { @content; } }

@mixin large-desktop { @media (min-width: #{$extra-large-width}) { @content; } }