.section {
   width: 100%;
   margin-top: 25px;
   padding: 0 25px;  
   &-middle { text-align: center; }
   &-fluid { padding: 0; width: 100%; margin-top: 25px; }

   .section-3{
      &-left, &-right, &-middle {
         width: 30%;
         display: inline-block;
         vertical-align: middle;
         position: relative;
      }

      &-middle { float: left; width: 40%;}

      &-left { 
         float: left; 
         .section-icon {
            &-left { right: 0; }
            &-right {
               padding-right: 85px;
               text-align: right;
            }
         }
      }

      &-right {
         float: right; 
         .section-icon {
            &-left { left: 0; }
            &-right {
               padding-left: 85px;
            }
         }
      }
   }

   .section-icon {
      margin: 155px 0;
      position: inherit;
      h4 {font-size: 20px;}
      p { margin-top: 10px; line-height: 26px; }
      &-left { position: absolute; top: 0; i { font-size: 50px; } }
     
   }
   .section-album-image {
      &-square, &-rectangle, &-vertical {
         float: left;
         background-color: white;
         margin: 5px;
         border-radius: 5%;
      }
      &-square {
         width: 24.9%;
         height: 280px;
      }

      &-rectangle {
         width: 48%;
         height: 280px;
      }

      &-square:last-child, &-rectangle:first-child { margin-right: 0; }

      &-vertical{
         margin-left: 0;
         height: 570px;
         width: 25%;
      }
   }
}